@use "../global/variables.scss";

.about {
  padding: 0 30px;

  @media (min-width: variables.$tablet-width) {
    padding: 0 50px;
  }

  @media (min-width: variables.$desktop-width) {
    padding: 0 225px;
  }
}

.about__title {
  font-weight: 700;
  font-size: 25px;
}

.about__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.about__item {
  font-weight: 700;
  line-height: 20px;
  color: variables.$black;
}
